import styled from 'styled-components';
import { useRef } from 'react';

import { Outro } from 'components';
import { COLOR, YEAR, BREAK_POINT } from 'utils/constants';

function Result({
  loading,
  user,
  dataofAll = [{}],
  dataof2024 = [{}],
  mostof2024 = [{}],
}) {
  const name = user?.login;
  const startDate = user?.created_at.slice(0, 10);
  const countDate = Math.round(
    (new Date() - new Date(user?.created_at)) / (1000 * 60 * 60 * 24) + 1
  );
  const resAll = [];
  dataofAll.forEach((e) => {
    resAll[e.name] = e.counts;
  });
  const res2024 = [];
  dataof2024.forEach((e) => {
    res2024[e.name] = e.counts;
  });
  const { sortedDate, sortedLang, sortedRepo } = mostof2024;

  const resRef = useRef();
  const res = resRef.current;

  return loading ? (
    <p>loading . . .</p>
  ) : (
    <>
      <Section ref={resRef}>
        <h2 className='sr-only'> </h2>
        <div>
           GitHub     <br />
          <span>'{startDate}'</span>  <span>'{countDate}'</span>
          .
        </div>

        <div>
          <span> {name}</span> 
          <ul>
            <li>
              - <span>{resAll.commits}</span>  
            </li>
            <li>
              - <span>{resAll.issues}</span> 
            </li>
            <li>
              - <span>{resAll.pr}</span> PR
            </li>
            <li>
              - <span>{resAll.repo}</span>  .
            </li>
          </ul>
        </div>

        <div>
          <span> {YEAR}</span> 
          <ul>
            <li>
              - <span>{res2024.commits}</span> 
            </li>
            <li>
              - <span>{res2024.issues}</span> 
            </li>
            <li>
              - <span>{res2024.pr}</span> PR
            </li>
            <li>
              - <span>{res2024.repo}</span>  .
            </li>
          </ul>
        </div>

        <div>
          <span>     </span>  .
          <ol>
            {sortedLang.slice(0, 3).map((ele, ind) => (
              <li key={ind}>
                <span>{ele.name}</span>
              </li>
            ))}
            {sortedLang.length > 3 && (
              <li>
                {'  '}
                {sortedLang.slice(3).map((ele, ind) => (
                  <span key={ind}>
                    {ele.name}
                    {ind < sortedLang.slice(3).length - 2 ? ', ' : ''}
                  </span>
                ))}
                {' ...'}
              </li>
            )}
          </ol>
        </div>

        <div>
          <span>      </span> <br />
          <span>{sortedDate?.name}</span>  <span>{sortedDate?.counts}</span>
            !
        </div>

        <div>
          <span>      </span> <br />
          <span>{sortedRepo?.name}</span>  <span>{sortedRepo?.counts}</span>
            !
        </div>
      </Section>
      <Outro name={name} res={res} />
    </>
  );
}

const Section = styled.section`
  text-align: left;
  color: ${COLOR.inactive};
  div {
    position: relative;
    left: 3rem;
  }
  div::before {
    position: absolute;
    left: -3rem;
    content: '> ';
    width: 1rem;
    height: 1rem;
  }
  ol {
    margin-left: 4rem;
  }
  div ~ div {
    margin-top: 5rem;
  }

  .userName {
    text-align: center;
    margin-bottom: 4rem;
  }

  @media (max-width: ${BREAK_POINT.tablet}px) {
    /* width: 30rem; */
    div {
      position: relative;
      left: 2rem;
    }
    div::before {
      left: -2rem;
    }
    div ~ div {
      margin-top: 3rem;
    }
  }
`;

export default Result;
